
import React, { useState, useEffect } from "react";
import {
  Box,
  Flex,
  Heading,
  Text,
  Button,
  VStack,
  HStack,
  Tag,
  Link,
  useColorModeValue,
} from "@chakra-ui/react";

const projects = [
  {
    title: "Project Alpha",
    category: "Web App",
    image: "https://images.unsplash.com/photo-1460925895917-afdab827c52f?w=800&q=80",
  },
  {
    title: "Beta Design",
    category: "UI/UX",
    image: "https://images.unsplash.com/photo-1558655146-d09347e92766?w=800&q=80",
  },
  {
    title: "Gamma API",
    category: "Backend",
    image: "https://images.unsplash.com/photo-1555066931-4365d14bab8c?w=800&q=80",
  },
];

export default function SimplePersonalSite() {
  const [index, setIndex] = useState(0);
  const bgColor = useColorModeValue("white", "gray.900");
  const subTextColor = useColorModeValue("gray.600", "gray.400");

  // Auto-scroll effect
  useEffect(() => {
    const interval = setInterval(() => {
      setIndex((prev) => (prev + 1) % projects.length);
    }, 3500);
    return () => clearInterval(interval);
  }, []);

  return (
    <Flex direction={{ base: "column", md: "row" }} h="100vh" overflow="hidden">
      
      {/* LEFT: AUTO-SCROLLING CAROUSEL */}
      <Box 
        flex="1" 
        position="relative" 
        bg="black" 
        h={{ base: "40vh", md: "100vh" }}
      >
        {projects.map((p, i) => (
          <Box
            key={i}
            position="absolute"
            inset="0"
            bgImage={`url(${p.image})`}
            bgSize="cover"
            bgPosition="center"
            // Use Chakra's transition for pure CSS fading
            opacity={index === i ? 1 : 0}
            transition="opacity 1.2s ease-in-out"
            _after={{
              content: '""',
              position: "absolute",
              inset: "0",
              bg: "blackAlpha.600",
            }}
          >
            <VStack
              h="full"
              justify="center"
              align="center"
              spacing={2}
              position="relative"
              zIndex="1"
            >
              <Text color="teal.300" fontWeight="bold" fontSize="xs" letterSpacing="widest">
                {p.category.toUpperCase()}
              </Text>
              <Heading color="white" size="xl">
                {p.title}
              </Heading>
            </VStack>
          </Box>
        ))}
        
        {/* Indicators */}
        <HStack position="absolute" bottom="8" left="50%" transform="translateX(-50%)" zIndex="2">
          {projects.map((_, i) => (
            <Box
              key={i}
              w={index === i ? "24px" : "8px"}
              h="8px"
              bg={index === i ? "teal.300" : "whiteAlpha.500"}
              borderRadius="full"
              transition="all 0.4s"
            />
          ))}
        </HStack>
      </Box>

      {/* RIGHT: ABOUT ME */}
      <Flex 
        flex="1" 
        bg={bgColor} 
        align="center" 
        justify="center" 
        p={12}
        textAlign={{ base: "center", md: "left" }}
      >
        <VStack align={{ base: "center", md: "start" }} spacing={6} maxW="md">
          <Heading size="2xl" lineHeight="shorter">
            Hi, I'm <Text as="span" color="teal.400">Your Name</Text>.
          </Heading>
          
          <Text fontSize="lg" color={subTextColor}>
            Iâ€™m a developer focused on building clean, functional, and user-friendly 
            interfaces. I love solving complex problems with simple solutions.
          </Text>

          <HStack spacing={3}>
            <Tag colorScheme="teal" variant="subtle">React</Tag>
            <Tag colorScheme="teal" variant="subtle">TypeScript</Tag>
            <Tag colorScheme="teal" variant="subtle">Chakra UI</Tag>
          </HStack>

          <Flex direction={{ base: "column", sm: "row" }} gap={4} w="full" pt={4}>
            <Button colorScheme="teal" size="lg" flex="1">
              View Projects
            </Button>
            <Button variant="outline" size="lg" flex="1">
              Contact Me
            </Button>
          </Flex>

          <HStack spacing={6} pt={4} color={subTextColor}>
            <Link _hover={{ color: "teal.400" }}>Twitter</Link>
            <Link _hover={{ color: "teal.400" }}>GitHub</Link>
            <Link _hover={{ color: "teal.400" }}>LinkedIn</Link>
          </HStack>
        </VStack>
      </Flex>
    </Flex>
  );
}